<script type="es6">
import { mapActions, mapState } from "vuex";
export default {
 data() {
    return {
     show:false,
     buyNum:'1',
     timer: null,
      timer2: null,
      isApprove:false,
    };
  },
   computed: {
    ...mapState("main", [
      "dogCoinContract",
      "FIVEBalance",
      "dogesInfo",
      "poolContract",
      "account",
    ]),
  },
   methods: {
    ...mapActions("main", [
      "add_DOGESpool",
      "add_dogCoinContract",
    ]),
    showFun: function () {
      this.show = true;
    },
     cols: function () {
      this.show = false;
    },
      buy: function (num) {
      this.buyNum =num ;
    },
      // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.dogesInfo.address;

      this.dogCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('usdt=========isApproveFun',res)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
        // USDT授权
    lpApprove: function () {
      if (this.dogCoinContract == null) {
        this.$toast('The wallet is not connected');
        return;
      }
      var address = this.dogesInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.dogCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success('Authorization success');
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
        protectAddress: function (addr, num) {
      if (addr == "0x0000000000000000000000000000000000000000") {
        return "--";
      }
      if (!addr) {
        return "--";
      }
      return (addr + "").length == 42
        ? addr.substring(0, num) + "****" + addr.substring(36, 42)
        : addr;
    },
    stake: function () {
      if (this.poolContract == null) {
        this.$toast(this.$t('The wallet is not connected'));
        return;
      }
      var _this = this;
      var account = _this.account;

    // if ((Number(_this.stakeAmount)  % 100) > 0 ) {
    //     _this.$toast(_this.$t("message.yu"));
    //     return;
    //   }
    //   if (Number(_this.stakeAmount) > Number(_this.coinBalance)) {
    //     _this.$toast(_this.$t("message.noBanner"));
    //     return;
    //   }
var  Balance = Number(_this.FIVEBalance / 10 ** 18);
  // return
      if (Balance < 0) {
        _this.$toast(_this.$t('Lack of balance!'));
        return;
      }
      if (Balance < (Number(_this.buyNum) * 500)) {
        _this.$toast(_this.$t('Lack of balance!'))
        return;
      }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.poolContract.methods
        .mint(_this.buyNum)
        .send({
          from: account,
					})
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success('successful!');
              _this.add_DOGESpool();
      _this.add_dogCoinContract();
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
   },
    mounted() {
    var _this = this;
    // if (this.$route.query.invite) {
    //   localStorage.setItem("invite", this.$route.query.invite);
    // }
    var getData = () => {
      this.add_DOGESpool();
      this.add_dogCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.dogesInfo ) {
          getData();
        } else {
          // this.getCoinBalance();
          this.isApproveFun();
          // this.FiveisApproveFun();
          // this.GrccisApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();
  
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  }}
</script>

