import { setStorage, getStorage } from '@/assets/js/units.js';
import getWeb3 from '@/assets/js/getWeb3.js'
import DOGES from '@/assets/coin_abi/DOGES.json'
import invitation from '@/assets/pool_abi/invitation.json'
import DOGE_pool from '@/assets/pool_abi/pool_DOGE.json'
const state = {
  /*存储用户信息全局变量*/
  account: JSON.parse(getStorage('account') || null),
  dogCoinContract: JSON.parse(getStorage('dogCoinContract') || null),
  Balance: JSON.parse(getStorage('Balance') || 0),
  FIVEBalance: JSON.parse(getStorage('FIVEBalance') || 0),
  lpCoinContract: JSON.parse(getStorage('lpCoinContract') || null),
  poolContract: JSON.parse(getStorage('poolContract') || null),
  dogesInfo: JSON.parse(getStorage('dogesInfo') || null),
  invitationContract: JSON.parse(getStorage('invitationContract') || null),
  transferContract: JSON.parse(getStorage('transferContract') || null),
}

const getters = {
  /*获取用户信息*/
  getIsApprove: function (state) {
    let isApprove = null;
    if (state.isApprove) {
      isApprove = state.isApprove;
    } else {
      isApprove = JSON.parse(getStorage('isApprove') || null);
    }
    return isApprove
  },

}

const mutations = {
  set_FIVEBalance: (state, data) => {
    state.FIVEBalance = data;
  },
  set_dogCoinContract: (state, data) => {
    state.dogCoinContract = data;
  },
  set_Balance: (state, data) => {
    state.Balance = data;
  },
  set_account: (state, data) => {
    state.account = data;
  },
  set_invitationContract: (state, data) => {
    state.invitationContract = data;
  },
  set_transferContract: (state, data) => {
    state.transferContract = data;
  },
  set_poolContract: (state, data) => {
    state.poolContract = data;
  },
  set_dogesInfo: (state, data) => {
    state.dogesInfo = data;
  },
}

const actions = {

  add_DOGESpool: ({ commit }, data) => {
    let info = {};
    getWeb3.Init(DOGE_pool, '0xdc0f2aef05b4590a10fb347443e1493ff00fef9b', (contract, addr, balance) => {
      // 0xe236bfb110aef0b2a5ce08e34fcbdc751e6e31c1
      commit('set_poolContract', contract);
      commit('set_account', addr);
      commit('set_Balance', balance);
      info.address = '0xdc0f2aef05b4590a10fb347443e1493ff00fef9b'
      contract.methods.getInfo(addr).call({
        from: addr,
      }).then((res) => {
    
        info.info = res;
        console.log(res, info)
        commit('set_dogesInfo', info);
      })

    })
  },


  // // 锁粉
  // add_invitationContract: ({ commit }, data) => {
  //   var thisArr = [
  //     '0xb8f85f478d39affae2b0f27a3cdf310341e2c8ed',
  //     '0xa4159092d871843fdb75dd0e2ef263a9bb5d2e90',
  //     '0x9eb2425bb28300424839b9448107bbebe566bd7b',
  //   ];
  //   var thisIndex = Math.floor((Math.random() * thisArr.length));
  //   var addrH = thisArr[thisIndex]
  //   localStorage.setItem('adrr',addrH );
  //   console.log(addrH)
  //   getWeb3.Init(invitation, addrH, (contract, addr, balance) => {
  //     commit('set_invitationContract', contract);

  //   })
  // },

  // // 转账
  // add_transferContract: ({ commit }, data) => {
  //   getWeb3.Init(invitation, '0x7a55a4888979f43bd983c9ab15f64ee58eca49d0', (contract, addr, balance) => {
  //     commit('set_transferContract', contract);

  //   })
  // },

  // 获取DOG币合约对象
  add_dogCoinContract: ({ commit }, data) => {
    getWeb3.Init(DOGES, '0x55d398326f99059fF775485246999027B3197955', (contract, addr, balance) => {
      commit('set_dogCoinContract', contract);
      commit('set_account', addr);
      contract.methods
        .balanceOf(addr)
        .call()
        .then((res) => {
          console.log(web3.utils.fromWei(res, "Gwei"))
          commit('set_FIVEBalance', res)
        });

    })
  },
}
export default {
  namespaced: true,
  state,
  mutations,
  getters,
  actions
}




